<template>
  <section class="news">
<!--    <Loader v-if="isLoading" class="news__loaderBtn"/>-->
    <BackBtn class="news__backBtn"/>
    <h1 class="news__header">{{ t('news.title') }}</h1>
    <div v-for="(item, i) in newsData" :key="i" class="news__item">
      <div class="news__item__header">
        <h2 class="news__item__title">{{ item.title }}</h2>
        <h3 class="news__item__date">{{ item.date }}</h3>
      </div>
      <p class="news__item__text">{{ item.text }}</p>
    </div>

    <h2
      @click="loadMoreNews"
      class="news__more-news"
    >{{ t('news.moreNews') }}</h2>
  </section>
</template>

<script>
import { useI18n } from 'vue-i18n'
import { ref, watch, onMounted } from 'vue'
import BackBtn from '@/components/UIKit/BackBtn'
import moment from 'moment'
import { useActions } from '@/plugins/vuex'
import { MODULES_NAMES } from '@/common/modules'

export default {
  name: 'News',
  components: { BackBtn },
  setup () {
    const { t, locale } = useI18n()
    const newsData = ref([])

    const {
      setIsLoading
    } = useActions(MODULES_NAMES.LOADING)

    watch(locale, () => {
      updateDatesLocale()
    })

    const updateDatesLocale = () => {
      newsData.value.forEach(item => {
        item.date = moment(item.globalDate).format('DD MMM YYYY')
      })
    }

    onMounted(async () => {
      try {
        setIsLoading(true)
        const res = await loadNews()
        newsData.value = res.data
      } catch (e) {
        console.log(e)
      } finally {
        setIsLoading(false)
      }
    })

    const loadMoreNews = async () => {
      try {
        setIsLoading(true)
        const res = await loadNews()
        newsData.value = [...newsData.value, ...res.data]
      } catch (e) {
        console.log(e)
      } finally {
        setIsLoading(false)
      }
    }

    const loadNews = () => {
      return new Promise(resolve => {
        setTimeout(() => {
          const tempData = {
            data: [
              {
                title: 'Merry Christmas!',
                globalDate: new Date(Date.now() - 86400000),
                date: moment(new Date(Date.now() - 86400000)).format('DD MMM YYYY'),
                text: 'Fees reduced to 1% till the 30th of December.'
              },
              {
                title: 'Reimagine the products',
                globalDate: new Date(),
                date: moment(new Date()).format('DD MMM YYYY'),
                text: 'With the help of the Play Library, you can use Play early in your design process to build out fully functional pages with just a few taps.'
              },
              {
                title: 'Merry Christmas!',
                globalDate: new Date(),
                date: moment(new Date()).format('DD MMM YYYY'),
                text: 'Fees reduced to 1% till the 30th of December.'
              }
            ]
          }
          resolve(tempData)
        }, 1000)
      })
    }
    return {
      t,
      newsData,
      loadMoreNews
    }
  }
}
</script>

